.skills-container {
  display: grid;
  grid-template-columns: repeat(4, 1fr); /* Four equal columns */
  gap: 20px;
  padding: 20px;
  background-color: #f9f9f9; /* Light background */
  border: 1px solid #ddd; /* Light border */
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1); /* Subtle shadow */
  margin-bottom: 20px;
  max-width: 100%; /* Ensures container doesn't overflow */
  box-sizing: border-box; /* Includes padding in width */
}

.skills-title {
  grid-column: span 4; /* Spans all columns */
  font-size: 22px;
  font-weight: bold;
  text-align: center;
  margin-bottom: 20px; /* Add space below the title */
}

.skill-category {
  display: flex;
  flex-direction: column; /* Stack skills vertically in each column */
}

.skill-category h3 {
  font-size: 18px;
  font-weight: 600;
  color: #333; /* Darker font color */
  margin-bottom: 10px;
  text-align: center; /* Center the category title */
}

.skill-category label {
  display: flex;
  justify-content: space-between; /* Spread label and dropdown */
  align-items: center;
  margin-bottom: 10px;
  font-size: 16px;
  color: #333;
}

.skill-category select {
  padding: 8px;
  font-size: 16px;
  background-color: #f0f0f0; /* Light grey dropdown */
  color: #333; /* Text color */
  border: 1px solid #ccc; /* Light grey border */
  border-radius: 5px;
  transition: border-color 0.3s ease;
  width: 75px; /* Fixed consistent width */
  box-sizing: border-box; /* Ensure padding and borders are included in width */
}

.skill-category select:hover {
  border-color: #999; /* Slightly darker border on hover */
}

.skill-category select:focus {
  outline: none;
  border-color: #555; /* Darker border on focus */
}

/* Media queries for Skills */
@media (max-width: 768px) {
  .skills-container {
    grid-template-columns: repeat(2, 1fr); /* Two columns for tablets */
    padding: 15px;
    gap: 15px;
  }

  .skills-title {
    font-size: 20px;
  }

  .skill-category h3 {
    font-size: 16px;
  }

  .skill-category label {
    font-size: 14px;
  }

  .skill-category select {
    font-size: 14px;
    padding: 6px;
  }
}

@media (max-width: 480px) {
  .skills-container {
    grid-template-columns: 1fr; /* Single column layout for small devices */
    padding: 10px;
  }

  .skills-title {
    font-size: 18px;
  }

  .skill-category h3 {
    font-size: 14px;
  }

  .skill-category label {
    flex-direction: column;
    align-items: flex-start; /* Align label and select vertically for mobile */
    font-size: 14px;
  }

  .skill-category select {
    width: 100%; /* Full width for dropdown on small screens */
  }
}
