.secondary-attributes-container {
  display: grid;
  grid-template-columns: 1fr 1fr; /* Two-column layout */
  gap: 20px;
  padding: 20px;
  background-color: #f5f5f5;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  margin-bottom: 20px;
}

.secondary-attributes-container label {
  display: flex;
  flex-direction: column;
  font-size: 16px;
  font-weight: 600;
  color: #333;
}

.secondary-attributes-container input {
  margin-top: 5px;
  padding: 8px;
  font-size: 16px;
  border-radius: 5px;
  border: 1px solid #ccc;
}

.secondary-attributes-container .attribute-display {
  margin-top: 5px;
  padding: 10px;
  background-color: #e0e0e0;
  font-size: 16px;
  text-align: center;
  border-radius: 5px;
}

/* Media queries for Secondary Attributes */
@media (max-width: 768px) {
.secondary-attributes-container {
  grid-template-columns: 1fr; /* Single column layout on tablets */
  gap: 15px;
  padding: 15px;
}

.secondary-attributes-container label {
  font-size: 14px;
}

.secondary-attributes-container input,
.secondary-attributes-container .attribute-display {
  font-size: 14px;
}
}

@media (max-width: 480px) {
.secondary-attributes-container {
  grid-template-columns: 1fr; /* Single column layout for small devices */
}
}
